const Section2 = ({info}) => {
  const [mouseEnterIndex, setMouseEnterIndex] = useState(-1)
  const [isMobile] = useState(checkIsMobile())
  const onMouseEnter = (index) => {
    setMouseEnterIndex(index)
  }
  const onMouseLeave = () => {
    setMouseEnterIndex(-1)
  }
  return (
    <section className="hero section2">
      <div className="container">
        <div className="titleArea">
          <div className="titleText">{info.title}</div>
        </div>
        <div className="columns is-gapless relative is-mobile is-multiline">
          {info.columns.map((column, index) => (
            <div className={isMobile ? "column is-half" : "column is-3"}>
              <div className="img-title center">{column.title}</div>
              <div 
                className="img relative"
                style={{backgroundImage: `url(${column.image})`}}
                onMouseEnter={() => onMouseEnter(index)}
                onMouseLeave={() => onMouseLeave()}
              >
                <div
                  className="infos"
                  style={{visibility: (mouseEnterIndex === index || isMobile) ? 'visible' : 'hidden'}}
                >
                  {column.infos.map((info) => (
                    <div className="info">{info}</div>
                  ))}
                </div>
              </div>
            </div>
          ))}
        </div>
        <div className="bottomText">{info.bottomText}</div>
      </div>
    </section>
  )
}

