const App = () => {
  // let history = useHistory();
  let location = useLocation()
  const [inChinese, setInChinese] = useState(!(location.pathname.indexOf('/en') > -1))
  const [info, setInfo] = useState(infos[inChinese ? 'cn' : 'cn'])

  useEffect(() => {
    const isChinese = !(location.pathname.indexOf('/en') > -1)
    setInChinese(isChinese)
    setInfo(infos[inChinese ? 'cn' : 'cn'])
  }, [location])

  return (
    <div className="home">
      <Section1 info={info.section1} />
      <Section2 info={info.section2} />
      <Section3 info={info.section3} />
      <Section4 info={info.section4} />
      <Section5 info={info.section5} />
      <Section6 info={info.section6} />
      <Section7 info={info.section7} />
      <Section8 info={info.section8} />
    </div>
  )
}

export default App
